<template>
  <div id="hum_controls">
    <div class="slider" ref="slider">
      <div class="process" :style="{ width }"></div>
      <div
        class="thunk"
        ref="trunk"
        @touchstart.prevent="mouseDownHandler"
        @touchmove.prevent="mouseMoveHandler"
        @touchend.prevent="mouseUpHandler"
        :style="{ left }"
      >
        <div class="block"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: "controls",
    props: [ 'min', 'max', 'value', 'unit', 'minTemperature', 'maxTemperature', 'alert' ],
    data(){
      return {
        slider: null,
        thunk: null,
        mouseDownWidth: '',
        mouseDowndisX: '',
        per: this.value,
        marginleftval: '-10px'
      }
    },

    mounted(){
      if(this.per > 99){
        this.marginleftval = '-20px'
      } else {
        this.marginleftval = '-10px'
      }
      this.slider = this.$refs.slider
      this.thunk = this.$refs.trunk
    },
    computed:{
      scale(){
        return (this.per - this.min) / (this.max - this.min)
      },
      showValue(){
        return parseInt(this.minTemperature) +  this.scale * (parseInt(this.maxTemperature) - parseInt(this.minTemperature))
      },
      width(){
        if (this.slider) {
          return this.slider.offsetWidth * this.scale + 'px';
        } else {
          return 0 + 'px'
        }
      },
      left(){
        if (this.slider) {
          return this.slider.offsetWidth * this.scale  - this.thunk.offsetWidth / 2 + 'px';
        } else {
          return 0 + 'px'
        }
      }
    },
    watch:{
      showValue(cur,old) {},
      minTemperature(cur, old){
        this.per =  ((this.value-parseInt(this.minTemperature)) / (parseInt(this.maxTemperature) - parseInt(this.minTemperature)))*100
        if(this.per > 100){
          this.per = 100
        }
      },
      maxTemperature(cur, old){
        this.per =  ((this.value-parseInt(this.minTemperature)) / (parseInt(this.maxTemperature) - parseInt(this.minTemperature)))*100
        if(this.per > 100){
          this.per = 100
        }
      },
      value(cur, old){
        if(Math.round(this.showValue) == cur){
          return
        }else {
          this.per =  ((this.value-parseInt(this.minTemperature)) / (parseInt(this.maxTemperature) - parseInt(this.minTemperature)))*100
          if(this.per>100){
            this.per = 100
          }
        }
      },
      alert(cur,old){}
    },
    methods: {
      mouseDownHandler(e){
        let _this = this
        let width = parseInt( _this.width );
        let disX = e.targetTouches[0].clientX;
        _this.mouseDownWidth = width
        _this.mouseDowndisX = disX
      },
      mouseMoveHandler(e){
        return
        // let self = this
        // let newWidth = e.targetTouches[0].clientX - self.mouseDowndisX + this.mouseDownWidth;
        // let scale = newWidth / self.slider.offsetWidth;
        // self.per = Math.ceil((self.max - self.min) * scale + self.min);
        // self.per = Math.max(self.per, self.min);
        // self.per = Math.min(self.per, self.max);
      },
      mouseUpHandler(){
        return
      }
    }
  }
</script>

<style>
  #hum_controls {
    width: 100%;
    height: 24px;
    margin: 0 auto;
    background: #c4c4c4;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
    border-radius: 12px;
  }
  #hum_controls .slider {
    position: relative;
    width: 85.4%;
    height: 24px;
    margin: 0 auto;
    background: none;
    cursor: pointer;
  }
  #hum_controls .slider .process {
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 24px;
    background: none;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
    border-radius: 12px;
  }
  #hum_controls .slider .thunk {
    position: absolute;
    left: -20px;
    top: 2px;
    width: 40px;
    height: 20px;
    background: #fff;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    border-radius: 10px;
  }
  #hum_controls .slider .thunk .block {
    width: 40px;
    height: 20px;
    background: #fff;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
    border-radius: 10px;
  }
  #hum_controls .slider .tips {
    position: absolute;
    /*left: -10px;*/
    top: -50px;
    font-size: 40px;
    color: #000;
    font-family: Arial;
    animation: breathe 3s linear infinite;
    color:#000;
  }
  #hum_controls .slider .tips span {
    font-size: 24px;
    vertical-align: top;
    color:#000;
  }
  /* @keyframes breathe {
    0% { opacity: .4; }
    50% { opacity: 1; }
    100% { opacity: .4; }
  } */
</style>



